
import service from 'service';
import Swiper from 'vux/src/components/swiper/swiper.vue'

import VueBackTop from 'components/common/backTop.vue'
  export default{
    components:{
      Swiper,
      VueBackTop
    },
    data(){
      return {
        title:'',
        tabId:0,
        backTopShow:false,
        data:{},
        bannerList:[],
        commodity_list:[],
        selectedColor:{},
        selectedSize:{}
      }
    },
    computed:{
      getLoginStatus(){
        return this.$store.getters.getLoginStatus;
      }
    },
    methods:{
      appendToCart(){
        let me = this;
        this.$store.dispatch('appendNewGoods',{
          id:me.data.id,
          commodityId:this.selectedSize.commodity_id
          }).then(()=>{
          this.$router.push({name:'cart'});
        });
      },
      selectSize(item){
        let me = this;
        me.selectedSize.active = false;        
        me.selectedSize = item;
        me.$set(item,'active',true);
      },
      selectColor(item){
        let me = this;
        console.log(item);
        me.selectedColor.active = false;
        me.title = item.name;
        me.selectedColor = item;
        me.$set(item,'active',true);
        me.selectSize(item.goods_list[0]);
      },
      getBannerList(data){
        let me = this;
        data.forEach(item=> {
          let a = {};
          a.img = item;
          a.title = '';
          a.url = '';
          me.bannerList.push(a);
        });
      },
      setDefaultVal(data){
        let me = this;
        me.data = data;
        me.title = data.name;
        me.commodity_list = data.commodity_list;
        me.selectColor(me.commodity_list[0]);
        me.selectSize(me.commodity_list[0].goods_list[0]);
      },
      getDetail(){
        let me = this,
          id = me.$route.query.id;
        service.getProduct(id).get().then(data=>{
          let result = data.body.data;
          me.getBannerList(result.gallery_img);
          me.setDefaultVal(result);
        })
      }
    },
    created(){
      this.getDetail();
    }
  }



