export default{
  name:'vue-back-top',
  props:{
    callback:{
      type:Function
    },
    node:{
      default:()=>{
        return document.body
      }
    },
    show:{
      type:Boolean,
      default:false
    }
  },
  data(){
    return {}
  },
  methods:{
    goTop(){
      this.node.scrollTop = 0;
      if(typeof this.callback === 'function') {
        this.callback();
      }
    }
  }
}



