import service from 'service'
import VueAutoImg from 'components/home/autoImg.vue'
export default{
  components:{
    VueAutoImg
  },
  data(){
    return {
      list:[],
      txt:'',
      wordList:[]
    }
  },
  watch:{
    txt:function(val){
      let me = this;
      console.log(val);
      if(!me.wordList.length && val){
        me.getWordList();
      }
    }
  },
  methods:{
    getWordList(){
      let me = this;
      service.word_list.get().then(data=>{
        let result = data.body.data;
        me.wordList = result;
      })
    },
    getSearchDefault(){
      let me = this;
      service.search_default.get().then(data=>{
        console.log(data);
        let result = data.body.data;
        me.list = result;
      })
    }
  },
  created(){
    this.getSearchDefault();
  }
}



