export default{
  name: 'vue-broadcast',
  props: {
    list: {
      type: Array,
      default: []
    },
    logo: {
      type: String,
      default: ''
    },
    speed: {
      type: Number,
      default: 5000
    }
  },
  data(){
    return {
      timer: null,
      broadcastIndex: 0
    }
  },
  mounted(){
    let me = this;
    me.timer = setInterval(()=> {
      me.broadcastIndex++;
      if (me.broadcastIndex >= me.list.length) {
        me.broadcastIndex = 0;
      }
    }, me.speed)
  }
}



